import './Promocao.css'
import terno from '../img/terno.png'
function Promocao(){
    return(
        <div className="container-fluid background2">
            <div className="container mt-4">
                <div className="row">
                    <div className="col-md-7 mt-2">
                        <h2 className="condicao mt-4"> CONDIO IMPERDIVEL </h2>
                        <h3 className="compre"> Compre hoje e granta: </h3>
                        <ul className="descontos">
                            <li>- 12% de desconto e pagamento em at 3x sem juros;</li>
                            <li>- At 10 meses para confeccionar sua roupa. No se preocupe se suas medidas aumentaram ou diminuram, estar sempre elegante;</li>
                            <li>- Condio muito especial por tempo limitado;</li>
                            <li>- Poder presentear algum com uma roupa de alta costura, exclusiva.</li>
                        </ul>
                        <button className="gold-button mb-4"> Garanta essa condio especial! </button>
                    </div>
                    <div className="col-md-5">
                        <img src={terno} alt="" className="img-fluid img-terno"/>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Promocao